*{
  font-family: Arial, Helvetica, sans-serif;
}

body{
  padding: 60px;
}
input{
  width: 100%;
  color: gray;
  font-size:1.2rem;
}
.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.card{
  align-content: center;
  border-bottom: 1px solid gray;
  border-radius: 5px;
  margin: 5px 10px;
  overflow: hidden;
}

.card p {
  padding:5px;
  text-align: center;
}
.card img {
  max-height:170px;
  
}